body, input { color: #757575; }

h5 {
  margin-top: 0;
}

textarea,
input.text,
input[type="text"],
input[type="button"],
input[type="submit"],
.input-checkbox {
	-webkit-appearance: none;
	border-radius: 0;
}
input[type=text] { font-size: 1em; }
input[type=submit] { font-weight: bold; }

input[type="submit"]:not([disabled]):hover { color: #000; }
input[type="submit"]:not([disabled]):focus{ -webkit-transform: translateY(0.125rem); -ms-transform: translateY(0.125rem); transform: translateY(0.125rem) }

ul {
	margin: 0;
	padding: 0;
}

.App { margin: 1em; }

.c-top-right {
	position: absolute;
	top: .5em;
	right: .5em;
}

.contrast { color: #eeaa32; }

.inl { display: inline; }
.inl-b { display: inline-block; }

.low-c { text-transform: lowercase; }

.right { float: right; }



