.box {
	border: 2px solid #fbebcf;
	float: left;
	font-size: 1em;
	margin: 0 1em 1em 0;
	max-width: 300px;
	padding: 1em;
	position: relative;
	width: 100%;
}

.box h5 {
    color: #79510C;
	font-size: 1em;
	margin-bottom: .75em;
	text-transform: uppercase;
}



